import MarkdownPage from 'components/MarkdownPage';
import React from 'react';
import {graphql} from 'gatsby';
import Layout from 'components/Layout';
import {createLinkTutorial} from 'utils/createLink';
import {sectionListTasks} from 'utils/sectionList';
 
const Tasks = ({data, location}) => {
  return (
    <Layout location={location}>
      <MarkdownPage
        enableScrollSync
        createLink={createLinkTutorial}
        location={location}
        markdownRemark={data.markdownRemark}
        sectionList={sectionListTasks}
        titlePostfix=" &ndash; React"
      />
    </Layout>
  );
};
 
export const pageQuery = graphql`
   query TemplateTasksMarkdown($slug: String!) {
     markdownRemark(fields: {slug: {eq: $slug}}) {
       html
       frontmatter {
         title
         next
         prev
       }
       fields {
         path
         slug
       }
     }
   }
 `;
 
export default Tasks;
 